h1 {
    margin: 7%;
}

.weight-profile {
    display: flex;
    flex-direction: column;
    align-items: center;
    user-select: none;
}

/* prevent select and highlight color */

.card {
    user-select: none;
}

.card::selection {
    background: none;
}

.card::-moz-selection {
    background: none;
}
/* ---------------------------------- */

.card {
    /* height: 240.52px; */
    height: auto;
    width: 95%;
    max-width: 1024px;
    border-radius: 20px;
    background-color: #dedede;
    margin: 10px 0;
}

/* Card Header */

.card--header {
    position: relative;
}

.card--header-total {
    text-align: center;
}

.card--header-icon-reset {
    position: absolute;
    top: -3px;
    left: 40px
}

.card--header-icon-edit {
    position: absolute;
    top: -4px;
    right: 40px
}